<template>
<div class="subpage--main flexrow">
  <Card class="tree-menu">
    <div class="tree-handler">
      <Button class="btn" type="info" icon="ios-plus-outline" @click="addOrg">新增</Button>
      <Button class="btn" type="error" icon="ios-close-outline" @click="deleteOrg">删除</Button>
    </div>
    <Tree :data="baseData" @on-select-change="selectNode" />
  </Card>
  <Card class="flex__item pane--right">
    <div class="form-wrapper">
      <h1 class="title">{{isAddMode ? '新增节点' : '修改节点'}}</h1>
      <Form ref="form" :model="form" :rules="ruleValidate" :label-width="120">
        <FormItem v-show="false" label="机构ID" prop="id">
          <Input v-model="form.id" />
        </FormItem>
        <FormItem label="机构名称" prop="name">
          <Input v-model="form.name" />
        </FormItem>
        <FormItem label="机构编号" prop="code">
          <Input v-model="form.code" />
        </FormItem>
        <FormItem v-show="false" label="上级机构" prop="parentId">
          <Input v-model="form.parentId" disabled />
        </FormItem>
        <FormItem label="上级机构名称" prop="parentName">
          <Input v-model="form.parentName" disabled />
          <Button type="primary" @click="isSuperiorSelectModalShown = true">打开上级机构列表</Button>
        </FormItem>
        <FormItem label="备注" prop="description">
          <Input v-model="form.description" type="textarea" :autosize="{ minRows: 2, maxRows: 5 }" />
        </FormItem>
        <FormItem label="使用状态" prop="state">
          <RadioGroup v-model="form.state">
            <Radio label="1">启用</Radio>
            <Radio label="0">停用</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem class="form-handler">
          <Button type="primary" @click="submit">{{isAddMode ? '新增' : '保存'}}</Button>
          <Button type="ghost" @click="reset">重置</Button>
        </FormItem>
      </Form>
    </div>
  </Card>
  <Modal v-model="isSuperiorSelectModalShown" title="请选择上级机构">
    <Tree :data="baseData" style="margin-left: 30%" @on-select-change="selectNodeInModal" />
    <div slot="footer">
      <Button type="primary" size="large" long @click="isSuperiorSelectModalShown = false">确认</Button>
    </div>
  </Modal>
</div>
</template>
<script>
import Tree from '@/components/Tree'
import {
  padValue,
  setValue
} from '@/utils/object'

export default {
  components: {
    Tree
  },
  data() {
    return {
      form: {
        id: null,
        name: null,
        code: null,
        parentId: null,
        parentName: null,
        description: null,
        state: null
      },
      ruleValidate: {
        name: [{
          required: true,
          message: '机构名称不能为空',
          trigger: 'blur'
        }],
        state: [{
          required: true,
          message: '必须选择状态',
          trigger: 'blur'
        }]
      },
      baseData: [],
      isSuperiorSelectModalShown: false,
    }
  },
  computed: {
    isAddMode() {
      return this.form.id === null
    }
  },
  created() {
    this.loadOrgTree()
  },
  methods: {
    async loadOrgTree() {
      try {
        this.baseData = await this.$api.system.getOrgTree.send()
      } catch (e) {
        console.log(e)
      }
    },
    submit(name) {
      this.$refs.form.validate(async valid => {
        if (valid) {
          const methodName = this.isAddMode ? 'createOrg' : 'updateOrg'
          try {
            await this.$api.system[methodName].send(this.form)
            this.$Message.success('提交成功！')
            padValue(this.form, null)
            this.loadOrgTree()
          } catch (e) {
            this.$Message.error('提交失败！' + e.message)
          }
        } else {
          this.$Message.error('请填写必填项！')
        }
      });
    },
    reset() {
      this.$refs.form.resetFields()
    },
    async selectNode(nodes) {
      try {
        const data = await this.$api.system.getOrg.send({
          id: nodes[0].id
        })
        setValue(this.form, data)
      } catch (e) {
      }
    },
    selectNodeInModal(nodes) {
      const {
        id,
        name
      } = nodes[0]
      Object.assign(this.form, {
        parentId: id,
        parentName: name
      })
    },
    addOrg() {
      padValue(this.form, null)
      Object.assign(this.form, {
        state: '1',
      })
    },
    deleteOrg() {
      if (this.form.id !== null) {
        this.$Modal.confirm({
          title: '系统提示',
          content: '此记录删除后无法恢复，是否继续？',
          onOk: async () => {
            try {
              await this.$api.system.deleteOrg.send({
                id: this.form.id
              })
              this.$Message.success('删除成功！')
              this.loadOrgTree()
              padValue(this.form, null)
            } catch (e) {
              this.$Message.error('删除失败！' + e.message)
            }
          }
        })
      } else {
        this.$Message.error('请勾选需要删除的资源！')
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.tree-menu {
  overflow-y: auto;
  min-width: 350px;
}
.tree-handler {
  display: flex;
  justify-content: space-around;
  padding: 5px 0 8px;
  .btn {
    width: 45%;
  }
}

.pane--right {
  overflow-y: auto;
  margin-left: 10px;
}
</style>
